* {
  border: 0;
  border-radius: 0;
  outline: 0;
  padding: 0;
}

body {
  font-family: var(--font-family);
  font-size: var(--size_m);
  color: var(--text-primary);
  background: var(--bg-accent);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

*, body, pre, ul {
  margin: 0;
}

a {
  cursor: pointer;
  color: #1983ff;
  color: var(--text-link);
  text-decoration: none;
  transition: color .2s;
}

a:active, a:hover {
  color: #3994ff;
  color: var(--text-link-hover);
}

:root {
  --bg-accent: #f7f8fb;
  --bg-default: #fff;
  --control-bg: #00278b0d;
  --control-ghost-text: #002033cc;
  --text-ghost: #00203366;
  --text-link: #1983ff;
  --text-link-hover: #3994ff;
  --text-primary: #002033;
  --font-family: "YS Display", sans-serif;
  --line-height-l: 1.6em;
  --line-height-2xs: 1em;
  --size_xs: 12px;
  --size_m: 16px;
  --size_4xl: 40px;
  --spacing_l: .2em;
  --border-radius_m: 8px;
}

.input {
  background-color: var(--control-bg);
  height: 28px;
  padding-left: var(--line-height-2xs);
  padding-right: var(--line-height-2xs);
}

button {
  padding: 5px 10px;
}

button:hover {
  cursor: pointer;
}

.header {
  width: 100%;
  background: var(--bg-accent);
  border-bottom: 1px solid #0000;
  flex-wrap: wrap;
  align-items: center;
  transition: border-bottom-color .2s;
  display: flex;
  position: sticky;
  top: 0;
}

.bordered-header {
  border-bottom-color: var(--control-bg);
}

